___CSS_LOADER_EXPORT___.push([module.id, `* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

.main {
  width: 100%;
  height: 100vh;

  background: black;
}

.main h1 {
  color: white;
}

.result-cpf {
  margin-top: 2rem;

  color: white;
  font-weight: bold;
}

.container {
  width: 100%;
  height: 100vh;

  background: black;
  display: flex;
  align-items: center;
  justify-content: center;
}

.container .box {
  background: white;

  display: inline-block;
  padding: 1rem;
  border-radius: 10px;
}

.box h1 {
  font-weight: bold;
  margin-bottom: 1rem;
}

.box p {
  margin-top: 1rem;
  margin-bottom: 1rem;
}

.box button {
  display: flex;
  width: 100%;
  padding: 0.5rem;
  justify-content:center;

  margin-top: 1rem;
  border-radius: 12px;
  border: none;
  background: #d665d7;
  color: #fff;
  font-weight: bold;
  font-size: 1rem;
  cursor: pointer;
  transition: filter 0.2s;

  &:hover {
   filter: brightness(0.9)
  }
}
